@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;700&display=swap);
.search__input{font-size:1.6rem;padding-left:.5rem;border-radius:5px;border:2px solid #286B9E}.search__input::-webkit-input-placeholder{color:#303030}.search__input:-ms-input-placeholder{color:#303030}.search__input::-ms-input-placeholder{color:#303030}.search__input::placeholder{color:#303030}

.post-item{padding:2rem;min-width:35rem;max-width:80rem;width:60vw;margin:0 auto;background-color:rgba(72,90,184,0.5);border-radius:4px;box-shadow:0 0.4rem 0.8rem rgba(0,0,0,0.2);transition:background .4s ease-out, transform .3s ease-in}.post-item:hover,.post-item:active{background-color:rgba(72,90,184,0.8);box-shadow:0 0.4rem 1rem rgba(0,0,0,0.5);transform:translateY(-0.5rem) scale(1.008)}.post-item__title:link,.post-item__title:visited{display:inline-block;font-size:3.5rem;font-weight:700;color:#E4E9EC;margin-bottom:2rem}.post-item__date{margin-top:1rem;font-size:1.4rem;font-weight:800;color:#8b1916}

.home{background-color:#E4E9EC}.hero{padding-top:9rem;display:grid;grid-template-columns:repeat(auto-fit, minmax(200px, 1fr));margin-bottom:10rem;width:100%}@media only screen and (max-width: 43.75em){.hero{padding:0;display:block}}.hero__image{width:100%;text-align:center}.hero__image .illustration{width:100%}@media only screen and (max-width: 43.75em){.hero__image{margin-top:-5rem}}.hero__content{padding:0 2rem;margin:auto;text-align:left}@media only screen and (max-width: 43.75em){.hero__content{margin-top:-5rem}}.hero__content .heading-1{margin-left:-1rem;margin-bottom:0;color:#485AB8}.hero__content .heading-2{text-transform:unset;margin-bottom:2rem}.hero__content .paragraph-1{max-width:40ch;margin-bottom:2rem}.hero-social{display:flex;align-items:center}.hero-social__link:visited:not(:first-child),.hero-social__link:link:not(:first-child){margin-left:2rem}.hero-social__link:hover .hero-social__logo{fill:#de2e29;transform:scale(1.11) translateY(-0.2rem)}.hero-social__logo{width:3rem;fill:#303030;transition:.2s ease-in-out}.mid-section{display:flex;align-items:center;align-content:center;justify-content:space-between;background-color:#485AB8;padding:2rem;position:relative}.mid-section__item{height:5rem}.mid-section__item.center{width:6rem;position:absolute;left:50%;top:20%;transform:translate(-50%, -80%)}.post{padding:9rem 0 20rem;background-color:#122251}.post .heading-2{text-align:center;color:white;margin-bottom:5rem}.post .search{text-align:center;margin-bottom:8rem}.post .search__input{padding:1rem;width:40rem;font-size:2.5rem}.post-item{margin-bottom:5rem}

.footer{max-width:190rem;margin:0 auto;margin-top:-20rem;padding-bottom:10rem;padding-top:20rem;background:url(/mini-blog-frontend/static/media/footerFlame.df576ca9.svg);background-size:cover;background-position:top center;background-repeat:no-repeat}.footer__logo{display:block;width:8rem;margin:0 auto}

.btn{outline:none;border:none;border-radius:5px;padding:.8rem 1rem;transition:background .3s}.btn--primary{background-color:#485AB8;color:white}.btn--primary:hover{background-color:#394894}

.header{display:flex;justify-content:space-between;align-items:center;padding:1rem 2rem;z-index:1000}.header__left{display:flex;align-items:center}.header-logo{height:4.5rem}.header__right{display:flex;flex-grow:1;justify-content:space-between;align-items:center}.header__right .search{margin-left:auto;margin-right:5rem}.header__right .search__input{height:3.5rem;width:20rem}.header__right .nav-list{display:flex;list-style:none;align-items:center}.header__right .nav-list__item{font-size:1.6rem;padding:.8rem 1rem;border-radius:10px;transition:background .4s}.header__right .nav-list__item:not(:first-child){margin-left:8rem}@media only screen and (max-width: 75em){.header__right .nav-list__item:not(:first-child){margin-left:5rem}}@media only screen and (max-width: 56.25em){.header__right .nav-list__item:not(:first-child){margin-left:3rem}}.header__right .nav-list__item:hover{background-color:#de2e29}.header__right .nav-list__item:hover>a{color:white}.header__right .nav-list__link{text-decoration:none;color:black}.header__right .header__button{font-size:2rem;margin-left:8rem}@media only screen and (max-width: 43.75em){.header__right .header__button{display:none}}@media only screen and (max-width: 75em){.header__right .header__button{margin-left:4rem}}.header__toggle-nav{display:none;width:40px;height:40px;outline:none;border:none;background:unset}.header__toggle-nav__button{display:inline-block;width:25px;height:3px;background-color:black;position:absolute;left:0}.header__toggle-nav__button.active{background-color:transparent}.header__toggle-nav__button::after,.header__toggle-nav__button::before{transition:0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275)}.header__toggle-nav__button::after{content:'';position:absolute;top:10px;left:0;width:40px;height:100%;background-color:black}.header__toggle-nav__button::before{content:'';position:absolute;top:-10px;left:0;width:40px;height:100%;background-color:black}.header__toggle-nav__button.active::after{top:0;transform:rotate(135deg)}.header__toggle-nav__button.active::before{top:0;transform:rotate(-135deg)}@media only screen and (max-width: 43.75em){.header__toggle-nav{display:block;position:fixed;z-index:10;right:3rem}}@media only screen and (max-width: 43.75em){.header__right{display:none;background-color:#485AB8;position:fixed;flex-direction:column;justify-content:center;width:100%;height:100%;top:0;right:0;left:0;z-index:10}.header__right.show{display:flex}.header__right .search{margin-top:-10rem;position:absolute;top:20%;width:100%}.header__right .search__input{display:block;width:80%;height:unset;max-width:180rem;font-size:4rem;border:unset;margin:0 auto;padding:1rem;border-radius:px;border:2px solid #286B9E}.header__right .search__input::-webkit-input-placeholder{color:#303030}.header__right .search__input:-ms-input-placeholder{color:#303030}.header__right .search__input::-ms-input-placeholder{color:#303030}.header__right .search__input::placeholder{color:#303030}.header__right .nav-list{flex-direction:column;justify-content:center;align-items:left;flex:1 1}.header__right .nav-list__item{margin-left:0;font-size:4rem}.header__right .nav-list__item:not(:first-child){margin-left:0;margin-top:5rem}.header__right .nav-list__link{color:white}}


*{margin:0;padding:0}html{font-size:62.5%;box-sizing:border-box;background-color:#E4E9EC}@media only screen and (max-width: 75em){html{font-size:58%}}@media only screen and (max-width: 56.25em){html{font-size:56%}}@media only screen and (max-width: 37.5em){html{font-size:54%}}body{box-sizing:inherit;font-family:'Montserrat', sans-serif}input{font-family:"Montserrat", sans-serif}::selection{background-color:#ce2520;color:white}.container{max-width:190rem;margin:0 auto}.heading-1{font-size:16rem;font-weight:700;margin-bottom:1rem}.heading-2{font-size:4rem;margin-bottom:1rem;text-transform:uppercase}.paragraph-1{font-size:1.8rem;line-height:3.4rem}.decor{position:relative;z-index:1}.decor::after{display:inline-block;content:'';position:absolute;bottom:0;left:.5rem;height:20px;width:70%;background-color:#485AB8;z-index:-1}

